"use strict";
// 自定义全局方法

import Vue from 'vue';

var custom = {}

/**
 * 处理请求回调
 * @param res 响应内容
 * @param thisObj this指向
 * @param callBack 回调函数
 */
custom.handleCallBack = function (res, thisObj, callBack) {
    if (res.data.code === 0) {
        callBack();
        return;
    }
    // 登录失败提示
    if (res.data.code === 100) {
        thisObj.$Modal.error({
            title: "提示",
            content: res.data.msg,
            onOk: () => {
                thisObj.$router.push('/login')
            }
        })
    } else {
        thisObj.$Message.warning(res.data.msg);
    }
}

/**
 * axios请求
 */
custom.post = function (url, params, thisObj, callBack) {
    thisObj.axios.post(url, params)
        .then(res => {
            if (res.data.code === 0) {
                callBack(res.data);
            } else {
                if (res.data.code === 100) {
                    thisObj.$Modal.error({
                        title: "提示",
                        content: res.data.msg,
                        onOk: () => {
                            thisObj.$router.push('/login')
                        }
                    })
                } else {
                    thisObj.$Message.error(res.data.msg);
                }
            }
        })
        .catch(err => {
            console.error(err);
        })
}

/**
 * 显示删除弹窗
 * @param thisObj this指向
 * @param callBack 回调函数
 */
custom.showDelModal = function (thisObj, callBack) {
    if (thisObj.selectionIds.length === 0) {
        thisObj.$Message.warning("请先进行选择");
        return;
    }
    thisObj.$Modal.confirm({
        title: "删除确定",
        content: "<p>删除后将不能恢复，确定删除吗?</p>",
        onOk: () => {
            callBack();
        },
        onCancel: () => {
            thisObj.$Message.warning("您取消了删除");
        }
    });
}

/**
 * form赋值
 * @param 
 * @param
 */
custom.handleForm = function (form, row) {
    Object.keys(form).forEach(key => {
        let defaultValue = typeof form[key] === "string" ? "" : 0;
        form[key] = row[key] || defaultValue;
    });
    return form;
}

// 还可以在这个文件里面添加多个函数
custom.install = function (Vue) {
    Vue.prototype.$custom = custom
    Vue.custom = custom
}
export default custom